<template>
    <el-form :model="ruleForm" :rules="rule" ref="ruleForm" label-position="left" label-width="0px"
             class="demo-ruleForm login-container">
        <h3 class="title">Payroll Login</h3>
        <el-form-item prop=" user">
            <el-input type="text" v-model="ruleForm. user" auto-complete="off" placeholder=" user"></el-input>
        </el-form-item>
        <el-form-item prop="pwd">
            <el-input type="text" v-model="ruleForm.pwd" @keyup.enter.native="submitForm" auto-complete="off"
                      placeholder="pwd"></el-input>
        </el-form-item>
        <!--<el-checkbox v-model="checked" class="remember">记住密码</el-checkbox>-->
        <el-form-item style="width:100%;">
            <el-button type="primary"
                       style="width:100%;"
                       :disabled="disabled"
                       @click.native.prevent="submitForm"
                       @keyup.enter.native="submitForm"
                       :loading="logining">Login
            </el-button>
        </el-form-item>
    </el-form>
</template>

<script>
import QuRequest from '../core/QuRequest'
import Auth from '../core/Auth'

const quRequest = new QuRequest()

export default {
    data() {
        return {
            logining: false,
            ruleForm: {
                user: '',
                pwd: ''
            },
            rule: {
                user: [
                    {required: true, message: 'Please enter your cell phone number', trigger: 'blur'},
                ],
                pwd: [
                    {required: true, message: 'Please input a pwd', trigger: 'blur'},
                ]
            },
            checked: false,
            disabled: false
        };
    },
    methods: {
        submitForm() {

            quRequest.send(quRequest.Apis.qutuiba.member.login, this.ruleForm
            ).then(res => {
                this.$message.success('Login successful！');
                this.$router.push({name: 'GlobalSetting'});
            }).catch(err => {
                this.$message.error(`${ err.errmsg || 'This combination of Company Id ,User Id and Password is invalid' }`);
            }).fin(() => {
                this.loading = false;
            }).done();
        },
    }
}

</script>

<style lang="less" scoped>
.login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;

    .title {
        margin: 0px auto 40px auto;
        text-align: center;
        color: #505458;
    }

    .remember {
        margin: 0px 0px 35px 0px;
    }

}
</style>


