<template>
    <el-row class="container">
        <!--头部内容-->
        <el-col :span="24" class="header">
            <el-col :span="10" class="logo" :class="!isCollapse ?'logo-collapse-width':'logo-width'">
                {{ $t("header.title") }}
            </el-col>
            <el-col :span="4">
                <div class="tools" @click.prevent="collapse">
                    <i class="fa fa-align-justify"></i>
                </div>
            </el-col>
            <el-col :span="10" class="userinfo">
                <div class="user-info">
                    <span class="logout" @click="logout()">exit</span>
                </div>
            </el-col>
        </el-col>
        <el-col :span="24" class="main">
            <div class="menu-box" :class="isCollapse ? 'auto-width':''">
                <el-menu :default-active="$route.path"
                         mode="vertical"
                         class="el-menu-vertical-demo"
                         @open="handleOpen"
                         @close="handleClose"
                         router
                         :collapse="!isCollapse"
                         :default-openeds="['/global','/global/global-setting','/global/payroll-tep','/finance','/finance/to-review','/operation','/operation/operation/user','/operation/operation/balance','/operation/operation/user-info']" >
                    <template v-for="(item, index) in menu"
                              v-if="!item.meta.hidden">
                        <el-submenu :index="item.path" v-if="!item.meta.children">
                            <template slot="title">
                                <i :class="item.meta.icon"></i>
                                <span slot="title">{{ item.meta.label }}</span>
                            </template>
                            <el-menu-item
                                    v-for="child in item.children"
                                    :index="item.path + '/' + child.path"
                                    :key="child.path"
                                    v-if="!child.meta.hidden">
                                {{ child.meta.label }}
                            </el-menu-item>
                        </el-submenu>
                    </template>
                </el-menu>
            </div>
            <!--主内容-->
            <section class="content-container">
                <div class="grid-content bg-purple-light">
                    <el-col :span="24" class="breadcrumb-container">
                        <strong class="title">{{ $route.meta.label }}</strong>
                        <el-breadcrumb separator="/" class="breadcrumb-inner">
                            <el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
                                {{ item.meta.label }}
                            </el-breadcrumb-item>
                        </el-breadcrumb>
                    </el-col>
                    <el-col :span="24" class="content-wrapper">
                        <transition name="fade" mode="out-in">
                            <router-view></router-view>
                        </transition>
                    </el-col>
                </div>
            </section>
        </el-col>
    </el-row>
</template>

<script>
    import QuRequest from '../core/QuRequest'
    import Auth from '../core/Auth'
    import routerMap from '../router/index'
    import util from '../common/js/util'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {
                token: Auth.getToken(),
                isCollapse: true,
                info: {},
                menu: routerMap.routes,
                disabled: true
            }
        },
        computed: {
//            filterIndex() {
//                var index=[];
//                return this.menu.map((item) => {
//                    if(!item.meta.hidden){
//                        this.index.push(item.path);
//                    }
//                    return index;
//                });
//            },
        },
        methods: {
            isLogin: function () {
                if (!this.token) {
                    this.$router.push('/login');
                }
            },
            getMemberInfo() {
                quRequest.send(
                    quRequest.Apis.perm.member.getMember,
                ).then(res => {
                    this.info = res.data.member || {};
                }).catch(err => {
                    this.$message.error(`${ err.message || 'Unknown error' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
            },
            handleOpen() {
            },
            handleClose() {
            },
            //退出登录
            logout() {
                this.$confirm('confirm exit?', 'tips', {
                    confirmButtonText: 'Ok',
                    cancelButtonText: 'Cancel',
                }).then(() => {
                    quRequest.send(
                        quRequest.Apis.qutuiba.member.logout,
                    ).then(res => {
                       if(res.code==0){
                           this.$router.push('/login');
                       }else{
                           this.$message.error(`${ res.errmsg || 'Unknown error' }`);
                       }
                    })

                });
            },
            //折叠导航栏
            collapse() {
                this.isCollapse = !this.isCollapse;
            }
        },
        mounted() {
           // console.log(routerMap.routes)
           // this.isLogin();
           // this.getMemberInfo();
        }
    }

</script>

<style lang="less">

    .nprogress-container {
        position: fixed !important;
        width: 100%;
        height: 50px;
        z-index: 2048;
        pointer-events: none;

        #nprogress {
            @color: #004c32;

            .bar {
                background: @color;
            }
            .peg {
                box-shadow: 0 0 10px @color, 0 0 5px @color;
            }

            .spinner-icon {
                border-top-color: @color;
                border-left-color: @color;
            }
        }
    }

    .container {
        position: absolute;
        top: 0px;
        bottom: 0px;
        width: 100%;
        max-width: 100%;
        .auto-width {
            width: 230px;
        }
        .menu-box {
            background: #eef1f6;
        }

        .fa-user-o {
            margin: 0px 5px;
        }

        .header {
            height: 60px;
            line-height: 60px;
            background: #20a0ff;
            color: #fff;
            .userinfo {
                text-align: right;
                float: right;
                .select-language {
                    width: 120px;
                }
                .user-info {
                    display: inline-block;
                    span {
                        display: inline-block;
                        &.nickname {
                            margin-right: 15px;
                            font-size: 18px;
                        }
                        &.logout {
                            width: 60px;
                            height: 59px;
                            line-height: 59px;
                            font-size: 16px;
                            font-weight: bold;
                            text-align: center;
                            border-radius: 4px;
                            background: #20a0ff;
                            cursor: pointer;
                        }
                    }
                }
                .userinfo-inner {
                    cursor: pointer;
                    color: #fff;
                    img {
                        width: 40px;
                        height: 40px;
                        border-radius: 20px;
                        margin: 10px 0px 10px 10px;
                        float: right;
                    }
                }
                .el-select {
                    vertical-align: top;
                    margin-left: 20px;
                }
            }
            .logo {
                //width:230px;
                height: 60px;
                font-size: 22px;
                padding-left: 20px;
                padding-right: 20px;
                border-color: rgba(238, 241, 146, 0.3);
                border-right-width: 1px;
                border-right-style: solid;
                img {
                    width: 40px;
                    float: left;
                    margin: 10px 10px 10px 18px;
                }
                .txt {
                    color: #fff;
                }
            }
            .logo-width {
                width: 230px;
            }
            .logo-collapse-width {
                width: 65px
            }
            .tools {
                padding: 0px 23px;
                width: 14px;
                height: 60px;
                line-height: 60px;
                cursor: pointer;
                i.fa-align-justify {
                    line-height: 60px;
                }
            }
        }
        .main {
            display: flex;
            // background: #324057;
            position: absolute;
            top: 60px;
            bottom: 0px;
            overflow: hidden;
            aside {
                flex: 0 0 230px;
                width: 230px;
                // position: absolute;
                // top: 0px;
                // bottom: 0px;
                .el-menu {
                    height: 100%;
                    overflow-y: auto;
                }
                .collapsed {
                    width: 60px;
                    .item {
                        position: relative;
                    }
                    .submenu {
                        position: absolute;
                        top: 0px;
                        left: 60px;
                        z-index: 99999;
                        height: auto;
                        display: none;
                    }

                }
            }
            .menu-collapsed {
                flex: 0 0 60px;
                width: 60px;
                .el-menu {
                    width: 100% !important;
                }
            }
            .menu-expanded {
                flex: 0 0 230px;
                width: 230px;
                .el-menu {
                    width: 100% !important;
                }
            }
            .content-container {
                flex: 1;
                overflow-y: auto;
                padding: 20px;
                .breadcrumb-container {
                    .title {
                        width: 200px;
                        float: left;
                        font-size: 1rem;
                        font-weight: normal;
                    }
                    .breadcrumb-inner {
                        float: right;
                    }
                }
                .content-wrapper {
                    background-color: #fff;
                    box-sizing: border-box;
                }
            }
        }
    }
</style>


