import ApiInfo from './ApiInfo.js';

class QutuibaApiInfo extends ApiInfo {
    constructor({
        path,
        type = 'get',
        timeout = 5000,
        headers,
        project = 'qutuiba',
        author = '未定义'
    }) {
        super({
            path,
            type,
            timeout,
            headers,
            project,
            author
        });
    }
}

