import lazyLoading from './lazyLoading'
import Apis from '../core/Apis'

export default {
    path: '/global',
    meta: {
        icon: 'el-icon-setting',
        label: 'Company',
        hidden: false
    },
    component: require('../views/Home'),
    children: [
        {
            name: 'GlobalSetting',
            path: 'global-setting',
            component: lazyLoading('global/GlobalSetting'),
            meta: {
                label: 'Basic Info',
                hidden: false,
                api: [

                ]
            }
        },
        {
            name: 'PayrollTep',
            path: 'payroll-tep',
            component: lazyLoading('global/PayrollTep'),
            meta: {
                label: 'Payroll Template',
                hidden: false,
                api: [

                ]
            }
        },
        {
            name: 'PayrollTepAdd',
            path: 'payroll-tep-add',
            component: lazyLoading('global/PayrollTepAdd'),
            meta: {
                label: '',
                hidden: true,
                api: [

                ]
            }
        },
        {
            name: 'PayStep2',
            path: 'payStep2',
            component: lazyLoading('global/PayStep2'),
            meta: {
                label: 'payrolls',
                hidden: true,
                api: [
                    Apis.qutuiba.version.getList
                ]
            }
        },
        {
            name: 'PayStep1',
            path: 'PayStep1',
            component: lazyLoading('global/PayStep1'),
            meta: {
                label: 'payrolls',
                hidden: true,
                api: [
                ]
            }
        },
        {
            name: 'Package',
            path: 'package',
            component: lazyLoading('global/Package'),
            meta: {
                label: 'payrolls',
                hidden: true,
                api: [
                ]
            }
        },
        {
            name: 'Report',
            path: 'report',
            component: lazyLoading('global/Report'),
            meta: {
                label: 'Report',
                hidden: true,
                api: [
                ]
            }
        },
        {
            name: 'ReportDetail',
            path: 'reportDetail',
            component: lazyLoading('global/ReportDetail'),
            meta: {
                label: 'ReportDetail',
                hidden: true,
                api: [
                ]
            }
        }, {
            name: 'PayslipDetail',
            path: 'payslipDetail',
            component: lazyLoading('global/PayslipDetail'),
            meta: {
                label: 'PayslipDetail',
                hidden: true,
                api: [
                ]
            }
        },
        {
            name: 'CompanyInfo',
            path: 'companyInfo',
            component: lazyLoading('global/CompanyInfo'),
            meta: {
                label: 'CompanyInfo',
                hidden: true,
                api: [
                ]
            }
        },
        {
            name: 'Preview',
            path: 'preview',
            component: lazyLoading('global/Preview'),
            meta: {
                label: 'Preview',
                hidden: true,
                api: [
                ]
            }
        }

    ]
}



