//@ts-check
import axios from 'axios';
import q from 'q';

class BaseRequest {
    constructor() {}

    formatData(data){
        if(data && typeof data === 'object'){
            let query = []
            for (let key in data) {
                query.push(`${key}=${encodeURIComponent(data[key])}`);
            }
            return query.join('&');
        } else {
            return data;
        }
    }

    send({
        url,
        type = 'get',
        timeout = 8000,
        headers = {}
    }, body = {}) {
        let options = {
            url: url,
            method: type.toLocaleLowerCase(),
            timeout: 10000,
            headers: headers
        };

        // 处理 post 参数格式
        if (options.method === 'post') {
            let query = []
            for (let key in body) {
                query.push(`${key}=${encodeURIComponent(body[key])}`);
            }
            options.data = this.formatData(body); // query.join('&');
        } else if (options.method === 'get') {
            options.params = body;
        } else {
            options.data = body;
        }
        return q.resolve(axios(options));
    }
}

export default BaseRequest;



