import lazyLoading from './lazyLoading'
import Apis from '../core/Apis'

export default {
    path: '/operation',
    meta: {
        icon: 'fa fa-user-o',
        label: 'Employee',
        hidden: false
    },
    component: require('../views/Home'),
    children: [
        {
            name: 'User',
            path: 'user',
            component: lazyLoading('operation/user/User'),
            meta: {
                label: 'Basic Info',
                hidden: false,
                api: [
                ]
            }
        },
        {
            name: 'UserPackage',
            path: 'userPackage',
            component: lazyLoading('operation/user/UserPackage'),
            meta: {
                label: 'UserPackage',
                hidden: true,
                api: [
                ]
            }
        }
        , {
            name: 'PayItem',
            path: 'pay-item',
            component: lazyLoading('operation/user/PayItem'),
            meta: {
                label: 'Pay item',
                hidden: true,
                api: [

                ]
            }
        } , {
            name: 'PackageItem',
            path: 'package-item',
            component: lazyLoading('operation/user/PackageItem'),
            meta: {
                label: 'package item',
                hidden: true,
                api: [

                ]
            }
        }, {
            name: 'PackageItemAdd',
            path: 'package-item-add',
            component: lazyLoading('operation/user/PackageItemAdd'),
            meta: {
                label: 'PackageItemAdd',
                hidden: true,
                api: [

                ]
            }
        }
     ]
}



